.devices {
  padding: 40px 20px;
  background: #f8f8f8;

  &__title {
    color: #7e7e82;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 16px;
  }

  /* --- Сетка карточек --- */
  &__row,
  &__extra {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }

  &__extra {
    display: none;
    margin-top: 10px;
    animation: fadeIn 0.3s ease;

    &.show {
      display: grid !important;
    }

    &.hidden {
      display: none;
    }
  }

  &__card {
    background: #fff;
    border-radius: 8px;
    padding: 20px;
    height: 120px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;

    font-size: 16px;
    color: #333;

    position: relative;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0.05);
  }

  &__icon {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 16px;
    bottom: 16px;
  }

  .hidden {
    display: none;
  }

  /* --- Показать все --- */
  &__toggle {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    margin-top: 20px;

    color: #7e7e82;
    font-weight: 500;
    cursor: pointer;
    user-select: none;

    &:hover {
      color: #333;
    }
  }

  &__arrow {
    width: 16px;
    height: 16px;
    transition: transform 0.3s ease;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-5px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  // Swiper для мобильной версии
  .devicesSwiper {
    display: none;
  }

  // 💻 Планшеты
  @media (min-width: 768px) and (max-width: 1119px) {
    &__row,
    &__extra {
      grid-template-columns: repeat(3, 1fr);
    }

    // Скрываем 4-ю карточку в основной строке
    &__row &__card:nth-child(4) {
      display: none;
    }

    // Скрываем 4-ю карточку в дополнительной строке
    &__extra &__card:nth-child(4) {
      display: none;
    }
  }

  // 📱 Мобильная версия
  @media (min-width: 320px) and (max-width: 767px) {
    padding: 24px 0;
    margin-left: 0;
    margin-right: 0;

    &__row,
    &__extra,
    &__toggle {
      display: none !important;
    }

    .devicesSwiper {
      display: block;
      width: 100vw;
      min-height: 160px;
      padding: 10px 16px;
      box-sizing: border-box;
      margin-left: 0;
      position: relative;
      left: 0;
    }

    .devicesSwiper .swiper-wrapper {
      align-items: center;
    }

    .devicesSwiper .swiper-slide {
      width: auto !important;
      display: flex;
      justify-content: flex-start;
      box-sizing: border-box;
    }

    .devicesSwiper .swiper-pagination {
      margin-top: 6px;
      position: relative;
      bottom: auto;
      transform: none;
      display: flex;
      justify-content: center;
      gap: 6px;
      height: 20px;
    }
  }
}
